import Vue from 'vue';
import Router from 'vue-router';
Vue.use(Router);
var routes = [{
  path: '/',
  name: 'home',
  component: function component() {
    return import('@/views/home/index.vue');
  },
  meta: {
    title: '首页'
  }
}, {
  path: '/404',
  name: '404',
  component: function component() {
    return import('@/views/error/index.vue');
  },
  meta: {
    title: '404NotFound'
  }
}];
var router = new Router({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes: routes
}); // 添加新增模块的路由配置

function callback() {
  require(['./demo'], function (demo) {
    router.addRoutes(_toConsumableArray(demo.default));
    router.addRoutes([{
      path: '*',
      redirect: '/404',
      hidden: true
    }]);
  }).catch(function (err) {
    console.log(err.message);
  });
} // https://router.vuejs.org/zh/api/#router-addroutes


router.onReady(callback);
export default router;